
import { defineComponent , ref , computed ,watch } from 'vue'
import Container from "@/components/Container.vue"
import axios from 'axios'
import { useStore } from 'vuex'
import langDict from "@/components/language"

export default defineComponent({
    setup() {
        //vuex
        const store = useStore()
        //language
        var lang_code = computed(() => store.state.lang_code);
        var lang_dict = ref(langDict(lang_code.value));
        watch(lang_code, ()=>{
            lang_dict.value = langDict(lang_code.value);
        });
        //timetable
        const eventList: any = {
            '100m': {zh_TW: '100', en_US: '100M',ja_JP:'100M'},
            '400m': {zh_TW: '400', en_US: '400M',ja_JP:'400M'},
            '1500m': {zh_TW: '1500', en_US: '1500M',ja_JP:'1500M'},
            'JT': {zh_TW: '', en_US: 'Javelin Throw',ja_JP:''},
            'SP': {zh_TW: '', en_US: 'Shot Put',ja_JP:''},
            'LJ': {zh_TW: '', en_US: 'Long Jump',ja_JP:''},
            '400mR': {zh_TW: '4100', en_US: '4100 M Relay',ja_JP:'4100m '},
            '2000mMR': {zh_TW: '10200', en_US: 'Mixed 10200 M Relay',ja_JP:'10200m '},
        }
        const timetable: any = ref({});
        function getTimetable(){
            axios.get('https://athletics.nsysu.rikujo.xyz/nsysua_backend/homepage/nfac/timetable/fetch.php').then((response) => {
                var temp:any = response.data;
                for(var i = 0; i < temp.length; i++) {
                    temp[i].datetime = temp[i].datetime.substr(11,5);
                    if(temp[i].sex==1){
                        temp[i].sex = lang_code.value=='zh_TW'? '': 'Male';
                    }else if(temp[i].sex==2){
                        temp[i].sex = lang_code.value=='zh_TW'? '': 'Female';
                    }else{
                        temp[i].sex = lang_code.value=='zh_TW'? '': 'Mixed';
                    }
                    temp[i].event = eventList[temp[i].event][lang_code.value];
                    if(temp[i].phase==2){
                        temp[i].phase = lang_code.value=='zh_TW' ? '':'FNL';
                    }else{
                        temp[i].phase = lang_code.value=='zh_TW' ? '':'PRE';
                    }
                }
                timetable.value = temp;
            });
        }
        getTimetable();
        watch(lang_code, ()=>{
            getTimetable();
        });
        return {
            timetable,
            lang_code,
            lang_dict
        }
    },
    components: {
        Container,
    }
})
